import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

const Contact = () => (
  <Layout>
    <SEO title="Alma" />
        <div class="container">
            <h1 class="my-4">Site Alma</h1>
            <div class="row">
                <div class="col-md-8">
                    <img class="img-fluid" src="https://www.dropbox.com/s/9b5gwkxubhjrajk/1%20-%20Accueil-min.jpg?raw=1" alt=""/>
                </div>

                <div class="col-md-4">
                <h3 class="my-3">Description Projet</h3>
                <p>Le but du projet est de créer un site pour l'association de l'Alma qui comprend le Cinéma de L'Alma ainsi que la Radio Clin D'Oeil FM. Pour faire cela, j'ai décidé d'utiliser la librairie ReactJS couplé avec Bootstrap pour le css et Firebase pour la base de données.</p>
                <h3 class="my-3">Détails du Projet</h3>
                <ul>
                    <li>Language: Javascript (ReactJS), HTML5, CSS3 (Librarie Bootstrap)</li>
                    <li>Site Démo: <a href="https://sitealma.netlify.com/cinema/">https://sitealma.netlify.com/cinema/</a></li>
                    <li>Site Gestion Administrative (Compter 30sec pour le démarrage du serveur): <a href="https://cryptic-ocean-83804.herokuapp.com/admin/">https://cryptic-ocean-83804.herokuapp.com/admin/</a></li>
                    <li>Editeur de Code: Visual Studio Code</li>
                    <li>Maquette du Site: Adobe XD</li>
                </ul>
                </div>

                </div>
                <h3 class="my-4">Les Maquettes</h3>
                <hr/>
                <h4>Cinéma de l'Alma</h4>
                <div class="row">
                    <div class="col-md-3 col-sm-6 mb-4">
                    <a href="#">
                            <img class="img-fluid" src="https://www.dropbox.com/s/9b5gwkxubhjrajk/1%20-%20Accueil-min.jpg?raw=1" alt=""/>
                        </a>
                    </div>

                    <div class="col-md-3 col-sm-6 mb-4">
                    <a href="#">
                            <img class="img-fluid" src="https://www.dropbox.com/s/df8dnhay7ufc4d3/2%20-%20A%20l%27affiche-min.jpg?raw=1" alt=""/>
                        </a>
                    </div>

                    <div class="col-md-3 col-sm-6 mb-4">
                    <a href="#">
                            <img class="img-fluid" src="https://www.dropbox.com/s/9jec9ymqk3dknor/3%20-%20Theatre-min.jpg?raw=1" alt=""/>
                        </a>
                    </div>

                    <div class="col-md-3 col-sm-6 mb-4">
                    <a href="#">
                            <img class="img-fluid" src="https://www.dropbox.com/s/a3m1aoqf9co2q1x/4%20-%20Ballets-min.jpg?raw=1" alt=""/>
                        </a>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-3 col-sm-6 mb-4">
                    <a href="#">
                            <img class="img-fluid" src="https://www.dropbox.com/s/fizps4f5r693m9n/5%20-%20Cinema%20Scolaire-min.jpg?raw=1" alt=""/>
                        </a>
                    </div>

                    <div class="col-md-3 col-sm-6 mb-4">
                    <a href="#">
                            <img class="img-fluid" src="https://www.dropbox.com/s/m22gyqv3plweopy/6%20-%20Comedie%20Francaise-min.jpg?raw=1" alt=""/>
                        </a>
                    </div>

                    <div class="col-md-3 col-sm-6 mb-4">
                    <a href="#">
                            <img class="img-fluid" src="https://www.dropbox.com/s/5r5807bh53lsyga/7%20-%20Paiement-min.jpg?raw=1" alt=""/>
                        </a>
                    </div>
                </div>
                <hr/>
                <h4>Radio Clin d'Oeil FM</h4>
        </div>
  </Layout>
)

export default Contact
